import React from 'react';
import 'antd/dist/antd.css';
import { Table, Form } from 'antd';
const EditableContext = React.createContext();

class EditableTable extends React.Component {
  state = {
    data: []
  };

  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      const {
        dailyAds,
        weeklyAds,
        monthlyAds,
        periodAds
      } = this.props.generalStatistics;

      if (dailyAds) this.setState({ data: dailyAds.jobs });

      if (monthlyAds) this.setState({ data: monthlyAds.jobs });

      if (weeklyAds) this.setState({ data: weeklyAds.jobs });

      if (periodAds) this.setState({ data: periodAds.jobs });
    }
  }

  columns = [
    {
      title: ' ',
      dataIndex: 'job_Name',
      width: '5%',
      editable: true
    },
    {
      title: '',
      dataIndex: 'company.companyName',
      width: '5%',
      editable: true
    },
    {
      title: '',
      dataIndex: 'descreption',
      width: '10%',
      editable: true
    },
    {
      title: '',
      dataIndex: 'city',
      width: '10%',
      editable: true
    },
    {
      title: ' ',
      dataIndex: 'work_hours',
      width: '5%',
      editable: true
    },
    {
      title: ' ',
      dataIndex: 'work_days',
      width: '5%',
      editable: true
    },
    {
      title: '',
      dataIndex: 'salary',
      width: '5%',
      editable: true
    },
    {
      title: '',
      dataIndex: 'createDate',
      width: '15%',
      editable: true
    }
  ];

  render() {
    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          inputType: col.dataIndex === 'age' ? 'number' : 'text',
          dataIndex: col.dataIndex,
          title: col.title
        })
      };
    });

    return (
      <React.Fragment>
        <EditableContext.Provider value={this.props.form}>
          <Table
            bordered
            dataSource={this.state.data}
            columns={columns}
            rowClassName="editable-row"
            pagination={{
              onChange: this.cancel
            }}
          />
        </EditableContext.Provider>
      </React.Fragment>
    );
  }
}

const JobsGeneralStatistics = Form.create()(EditableTable);

export default JobsGeneralStatistics;
