import React from 'react';
import { Row, Collapse, Input } from 'antd';
import './style.scss';
import { withTranslation } from 'react-i18next';
import Footer from '../../../Footer';
import Navbar from '../../../Header/Navbar';
const { Panel } = Collapse;
const text = (
  <p style={{ paddingLeft: 24, paddingRight: '40px' }} className="qus-reply">
                 
                 .
              -  -
           
  </p>
);
const CommonQuestions = props => {
  const { i18n } = props;
  return (
    <Row style={{ background: '#fff' }}>
      <Navbar />
      <br />
      <br />
      <Row className="com-questions">
        <h3 className="com-title">{i18n.t('home.comQuestions')} </h3>
        <div className="questions">
          <div className="question">
            <Collapse
              bordered={false}
              className="cQues-collapse"
              expandIcon={panelProps => (
                <div>
                  {panelProps.isActive ? (
                    <i
                      className="fa fa-minus"
                      aria-hidden="true"
                      style={{ color: '#fff ', fontSize: '20px' }}
                    ></i>
                  ) : (
                    <i
                      className="fa fa-plus ques-plus-icon"
                      aria-hidden="true"
                      style={{ color: '#18233d ', fontSize: '20px' }}
                    ></i>
                  )}
                </div>
              )}
              expandIconPosition="right"
            >
              <Panel
                key="1"
                className="qus-name"
                header={
                  <p className="ques-title" style={{ marginBottom: '0' }}>
                              
                            
                  </p>
                }
              >
                <p
                  className="qus-reply"
                  style={{
                    paddingLeft: 24,
                    paddingRight: '40px'
                  }}
                >
                            
                             
                     .
                </p>
              </Panel>
              <Panel
                className="qus-name"
                header={
                  <p className="ques-title" style={{ marginBottom: '0' }}>
                             
                    ptway   
                  </p>
                }
                key="2"
              >
                {text}
              </Panel>
              <Panel
                className="qus-name"
                header={
                  <p className="ques-title" style={{ marginBottom: '0' }}>
                          
                  </p>
                }
                key="3"
              >
                {text}
              </Panel>
              <Panel
                className="qus-name"
                header={
                  <p className="ques-title" style={{ marginBottom: '0' }}>
                           
                  </p>
                }
                key="4"
              >
                {text}
              </Panel>
              <Panel
                className="qus-name"
                header={<p className="ques-title">    </p>}
                key="5"
              >
                {text}
              </Panel>
              <Panel
                className="qus-name"
                header={
                  <p className="ques-title" style={{ marginBottom: '0' }}>
                         
                  </p>
                }
                key="5"
              >
                {text}
              </Panel>
              <Panel
                className="qus-name"
                header={
                  <p className="ques-title" style={{ marginBottom: '0' }}>
                         
                  </p>
                }
                key="5"
              >
                {text}
              </Panel>
            </Collapse>
          </div>
        </div>
      </Row>
      <br />
      <br />
      <Row className="contact-us">
        <h2 className="title">   </h2>
        <form className="questions-form">
          <div className="inputs">
            <Input placeholder="" />
            <Input placeholder="" />
          </div>
          <Input.TextArea rows={4} placeholder="  " />
          <button className="send-btn"></button>
        </form>
      </Row>

      <Footer />
    </Row>
  );
};

export default withTranslation()(CommonQuestions);
