export default function education_level(key) {
  let level = [];
  if (key == 'HS') {
    level.push(
      { value: 'High-school-first-year', viewValue: ' ' },
      { value: 'High-school-second-year', viewValue: ' ' },
      { value: 'High-school-third-year', viewValue: ' ' },
      { value: 'High-school-fourth-year', viewValue: ' ' }
    );
    return level;
  } else if (key == 'BHO') {
    level.push(
      { value: 'University-first-year', viewValue: ' ' },
      { value: 'University-second-year', viewValue: ' ' },
      { value: 'University-third-year', viewValue: ' ' },
      { value: 'University-forth-year', viewValue: ' ' },
      { value: 'University-fith-year', viewValue: ' ' },
      { value: 'University-sixth-year', viewValue: ' ' },
      { value: 'University-seventh-year', viewValue: ' ' },
      { value: 'University-eigth-year', viewValue: ' ' },
      { value: 'University-ninth-year', viewValue: ' ' },
      { value: 'University-ten-year', viewValue: ' ' }
    );
    return level;
  } else if (key == 'MASTER') {
    level.push(
      { value: 'master-first-year', viewValue: ' ' },
      { value: 'master-second-year', viewValue: ' ' },
      { value: 'master-third-year', viewValue: ' ' }
    );
    return level;
  } else if (key == 'diploma') {
    level.push(
      { value: 'diploma-first-year', viewValue: ' ' },
      { value: 'diploma-second-year', viewValue: ' ' },
      { value: 'diploma-third-year', viewValue: ' ' },
      { value: 'diploma-fourth-year', viewValue: ' ' }
    );
    return level;
  } else if (key == 'Undergraduate') {
    level.push({ value: 'Undergraduate', viewValue: '' });
    return level;
  }
}
