import React from 'react';
import { Row, Col, Tabs } from 'antd';
import './style.scss';
import { withTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
const { TabPane } = Tabs;

const HowItWorks = () => {
  // const { i18n } = props;
  return (
    <Row className="services">
      <Col md={12} sm={24} style={{ direction: 'initial' }}>
        <Tabs
          defaultActiveKey="4"
          // animated={false}
          className="works-tab"
        >
          <TabPane tab=" " key="1">
                      
                      
            <br />
            <p className="tag-title"> </p>
            <span className="tag">
              _ &nbsp;#_&nbsp; #_# &nbsp;
            </span>
            <i
              className="fa fa-certificate"
              aria-hidden="true"
              style={{ color: '#049ad0' }}
            ></i>{' '}
            <br />
            <Link
              to="/user/signup"
              style={{ fontSize: '28px', color: '#18233d' }}
            >
              {'<'}   &nbsp; &nbsp;
            </Link>
          </TabPane>
          <TabPane tab=" " key="2">
                       
                        
                  
            <br />
            <p className="tag-title"> </p>
            <span className="tag">
              _ &nbsp;#&nbsp; ## &nbsp;
            </span>
            <i
              className="fa fa-file-text-o"
              aria-hidden="true"
              style={{ color: '#049ad0' }}
            ></i>{' '}
            <br />
            <Link
              to="/user/signup"
              style={{ fontSize: '28px', color: '#18233d' }}
            >
              {'<'}   &nbsp; &nbsp;
            </Link>
          </TabPane>
          <TabPane tab="" key="3">
                      
                      
                 
            <br />
            <p className="tag-title"> </p>
            <span className="tag">
               &nbsp;#&nbsp; #_# &nbsp;
            </span>
            <i
              className="fa fa-gg-circle"
              aria-hidden="true"
              style={{ color: '#049ad0' }}
            ></i>{' '}
            <br />
            <Link
              to="/user/signup"
              style={{ fontSize: '28px', color: '#18233d' }}
            >
              {'<'}   &nbsp; &nbsp;
            </Link>
          </TabPane>
          <TabPane tab="" key="4">
                        
                    
              .
            <br />
            <p className="tag-title"> </p>
            <span className="tag">
               &nbsp;#_&nbsp; ## &nbsp;
            </span>
            <i
              className="fa fa-calendar"
              aria-hidden="true"
              style={{ color: '#049ad0' }}
            ></i>{' '}
            <br />
            <Link
              to="/user/signup"
              style={{ fontSize: '28px', color: '#18233d' }}
            >
              {'<'}   &nbsp; &nbsp;
            </Link>
          </TabPane>
        </Tabs>
      </Col>
      <Col md={12} sm={24} className="services-tabs">
        <h2 className="title"></h2>
        <p className="desc">
                      
                    
        </p>
      </Col>
    </Row>
  );
};

export default withTranslation()(HowItWorks);
