import React from 'react';
import { Row, Input, Button, Alert, Cascader } from 'antd';
import _ from 'lodash';
import './style.scss';
import userSetting from '../../../services/adminSetting/user';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import statatisticsService from '../../../services/statisticsService';

const { allCities, allMajors, sMajor } = statatisticsService;
const { activateAccounts, addSubAdmin, exportCityMajorData } = userSetting;
class UserSetting extends React.Component {
  state = {
    status: false,
    ready: false
  };

  async componentDidMount() {
    const majors = await allMajors();
    this.setState({ majors });
    const cities = await allCities();
    this.setState({
      cities
    });
  }

  handleStatus = async () => {
    const activate = await activateAccounts();
    if (activate === 'Updated') alert('   ');
  };
  handleChange = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: value
    });
  };

  cityChange = (value, selectedOptions) => {
    this.setState({
      city: selectedOptions[0].id
    });
  };

  majorChange = (value, selectedOptions) => {
    this.setState(
      {
        major: selectedOptions[0].id
      },
      async () => {
        const { major } = this.state;
        const specialMajor = await sMajor(major);
        this.setState({ specialMajor });
      }
    );
  };

  specialMajorChange = (value, selectedOptions) => {
    this.setState({
      sub_major: selectedOptions[0]
    });
  };

  exportCityMajorData = async () => {
    const { city, major, sub_major } = this.state;
    await exportCityMajorData({
      city,
      major,
      sMajor: sub_major.id
    });
    this.setState({ ready: true });
  };
  addSubAdmin = async () => {
    const { firstName, lastName, email, password } = this.state;
    const newSubAdmin = await addSubAdmin({
      firstName,
      lastName,
      email,
      password
    });
    this.setState({
      newSubAdmin
    });
  };
  render() {
    const { newSubAdmin, majors, cities, ready, excelData } = this.state;
    return (
      <React.Fragment>
        <Row>
          <div className="activate-accounts-setting">
            <label>  </label>
            {/* <Switch
              onChange={this.handleStatusChange}
              checked={status}
              name="status"
              className="setting-switch"
            /> */}
            <Button
              onClick={this.handleStatus}
              name="status"
              className="activate-user-btn"
            >
              {' '}
              
            </Button>
          </div>
        </Row>
        <Row>
          <div className="export-city-major-data">
            <h3 className="title">   </h3>
            <Cascader
              className="dropdown-menu"
              options={majors}
              onChange={this.majorChange}
              placeholder="  "
            />
            <Cascader
              className="dropdown-menu"
              options={this.state.specialMajor}
              onChange={this.specialMajorChange}
              placeholder=" "
            />
            <Cascader
              className="dropdown-menu"
              options={cities}
              onChange={this.cityChange}
              placeholder=" "
            />
            <Button onClick={this.exportCityMajorData}> Download as XLS</Button>
            {ready && (
              <div>
                <ReactHTMLTableToExcel
                  id="test-table-xls-button"
                  className="download-table-xls-button"
                  table="table-to-xls"
                  filename="tablexls"
                  sheet="tablexls"
                  buttonText="ready to export"
                />
                <table id="table-to-xls" style={{ display: 'none' }}>
                  <tr>
                    <th>name</th>
                    <th>email</th>
                    <th>number</th>
                  </tr>
                  {excelData
                    ? excelData.map(elm => {
                        return (
                          <tr key={elm.number}>
                            <td>{elm.name ? elm.name : ''}</td>
                            <td>{elm.number ? elm.number : ''}</td>
                          </tr>
                        );
                      })
                    : ''}
                </table>
              </div>
            )}
          </div>
        </Row>
        <Row>
          <div className="add-admin">
            {_.isObject(newSubAdmin) && (
              <Alert message="  "></Alert>
            )}
            <h3 className="new-admin-title">  </h3>
            <div className="admin-information">
              <Input
                placeholder=" "
                name="firstName"
                onChange={this.handleChange}
              />
            </div>
            <div className="admin-information">
              <Input
                placeholder=" "
                name="lastName"
                onChange={this.handleChange}
              />
            </div>
            <div className="admin-information">
              <Input
                placeholder=" "
                onChange={this.handleChange}
                name="email"
              />
            </div>
            <div className="admin-information">
              <Input
                placeholder=" "
                type="password"
                name="password"
                onChange={this.handleChange}
              />
            </div>
            <Button className="add-admin-button" onClick={this.addSubAdmin}>
              
            </Button>
          </div>
        </Row>
      </React.Fragment>
    );
  }
}

export default UserSetting;
