import React from 'react';
import './style.scss';
import { Form, Icon, Input, Button, Checkbox, Alert } from 'antd';
import { Link } from 'react-router-dom';
import Footer from '../../Footer';
import { connect } from 'react-redux';
import { userLogin } from '../../../store/actions/userAction';

class UserLoginForm extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        const { username, password } = values;
        console.log('props---', this.props.user);

        const { login } = this.props;
        await login({
          username,
          password
        });
        const { history } = this.props;
        history.push('/user/home');
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { user } = this.props;
    return (
      <div className="user-login-container">
        <div className="form-container">
          <h3 className="login-form-title"> </h3>
          <Form onSubmit={this.handleSubmit} style={{ width: '100%' }}>
            <label className="login-form-label"> </label>
            <Form.Item>
              {getFieldDecorator('username', {
                rules: [
                  { required: true, message: '   ' }
                ]
              })(<Input prefix={<Icon />} />)}
            </Form.Item>
            <label className="login-form-label"> </label>
            <Form.Item>
              {getFieldDecorator('password', {
                rules: [{ required: true, message: '   ' }]
              })(<Input prefix={<Icon />} type="password" />)}
              <Link to="/user/resetPassword">  </Link>
            </Form.Item>
            <Form.Item>
              {getFieldDecorator('remember', {
                valuePropName: 'checked',
                initialValue: true
              })(<Checkbox></Checkbox>)}
              {user.error && (
                <Alert message={user.error.response.data} type="error" />
              )}
            </Form.Item>
            {/* <Form.Item className="check-user-existance"> */}
            <div className="login-btn-cont">
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button login-form-btn"
              >
                 
              </Button>
            </div>
            <div className="create-new-account">
                 <Link to="/user/signup">  </Link>
            </div>
            {/* </Form.Item> */}
          </Form>
        </div>
        <div style={{ width: '100%' }}>
          <Footer />
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ user }) => {
  return {
    user
  };
};
const mapDispatchToProps = dispatch => {
  return {
    login: params => {
      return dispatch(userLogin(params));
    }
  };
};

const UserLogin = Form.create({ name: 'UserLoginForm' })(UserLoginForm);

export default connect(mapStateToProps, mapDispatchToProps)(UserLogin);
